<template>
  <div class="year">
    <div class="buyheader">
      <h1>年度销量</h1>
    </div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">产品列表</el-breadcrumb-item>
      <el-breadcrumb-item>年度销量</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="center-bell">
      <ve-line :data="chartData" :settings="chartSettings"></ve-line>
    </div>
    




  </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },
  created: function () {
      this.chartData = {
        columns: ['date', '销量'],
        rows: [
          { 'date': '1月', '销量': 300 },
          { 'date': '2月', '销量': 600},
          { 'date': '3月', '销量': 800 },
          { 'date': '4月', '销量': 900},
          { 'date': '5月', '销量': 1000},
          { 'date': '6月', '销量': 800 }
        ]
      }
      this.chartSettings = {
        label: {
          normal: {
            show: true
          }
        }
      }
    }
};
</script>

<style lang="scss" scoped>
.center-bell{
  padding: 15px;
  a:last-child{
    margin-bottom: 0;
  }
  a{
    background: #eee;
    display: block;
    padding: 10px;
    margin: 10px 0;
    color: #461b93;
  }
}


</style>



